/** Media Queries **/
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.btns {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.btn {
  background: #FFF;
  padding: 0.7rem 1.8rem;
  border-radius: 1rem;
  border: 0;
  font-size: 1.3rem;
}

.btn-primario {
  background: #192bc2;
  color: #f4f1de;
  transition: background 0.3s ease;
}
.btn-primario:hover {
  cursor: pointer;
  background: rgb(21.25, 36.55, 164.9);
}

.btn-negro {
  background: #231f20;
  color: #f4f1de;
  transition: background 0.3s ease, color 0.3s ease, border 0.1s ease;
}
.btn-negro:hover {
  cursor: pointer;
  background: #f4f1de;
  color: #231f20;
  border: 2px solid #231f20;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Istok Web", serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background: #f4f1de;
  color: #020202;
}

p {
  color: #020202;
  font-size: 1.5rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #020202;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Istok Web", serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

::-webkit-scrollbar {
  display: none;
}

.navegacion-principal {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  background: rgb(238.175, 233.5863636364, 204.525);
}
.navegacion-principal a h2 {
  font-weight: 700;
  margin: 0;
}
.navegacion-principal a:last-child {
  position: relative;
}
.navegacion-principal a:last-child::before {
  content: " ";
  width: 0%;
  height: 0.1rem;
  background: black;
  display: block;
  position: absolute;
  bottom: 0.4rem;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.3s ease-in-out;
}
.navegacion-principal a:last-child:hover::before {
  width: 100%;
}

.header {
  margin: 2rem 1rem 0 1rem;
}
.header h1 {
  font-weight: 700;
  text-align: start;
  margin: 0;
}
.header p {
  margin: 0;
}